.start {
    text-align: center;
    background-color: rgba(255, 210, 180);
  }
  
  .full-screen-image {
    width: 100%;
    height: 100%;
  }
  
  .full-screen-image img {
    width: 100%;
  }
  
  .full-screen-image .map {
    width: 90%
  }

  .content-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
    align-items: center;
    justify-content: center;
    margin-top: 0px;

    background-image: url('/public/imgs/banner2.jpeg');
    background-size: cover;
    background-repeat: no-repeat;
  }
  
  .image-container img {
    max-width: 100%;
    height: auto;
  }
  
  .text-container {
    text-align: left;
    font-size: 35px;
    color: white;
  }



  .buy {
    align-items: center;
  }
  
  .buybtnstart {
    margin: 10px;
    height: 100px;
    width: 99%;
    background-color: rgb(255, 210, 180);
    border-radius: 5px;
    font-size: 30px;
    border: 0.1px solid rgb(238, 182, 145);
    box-shadow: 0px 5px 15px rgb(247, 193, 158);
    text-align: center;
    cursor: pointer;
    align-self: center;
  }

  .buybtnstart:hover {
    background-color: rgba(211, 145, 101, 0.7);
    box-shadow: 0px 1px 7px rgb(150, 150, 150);
  }